import { NgModule } from '@angular/core';
import { SharedModule } from '../shared/shared.module';  
import { AddressModule } from '../address/address.module';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { UploadModule } from '@progress/kendo-angular-upload'; 
 
import { CommissionRequestComponent } from './components/commission-request/commission-request.component';
import { CommissionRequestService } from './services/commissionRequest.service';

import { CommissionRequestRoutingModule } from './commission-routing.module';

@NgModule({
    imports: [
        SharedModule.forRoot(), 
        InputsModule,
        UploadModule,
        AddressModule,
        CommissionRequestRoutingModule
    ],
    exports: [
        CommissionRequestComponent
    ],
    declarations: [
        CommissionRequestComponent
    ],
    providers: [
        CommissionRequestService
    ]
})
export class CommissionRequestModule { }


