
import { Routes, RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';

import { CommissionRequestComponent } from './components/commission-request/commission-request.component';
import { CurrentUserResolver  } from '../shared/services/common/user-resolver.service';

const commissionRequestRoutes: Routes = [ 
    {
        path: ':commissionRequestId/:projectId/:quoteId',
        component: CommissionRequestComponent,
        resolve: { user: CurrentUserResolver }
    },
];

@NgModule({
    imports: [RouterModule.forChild(commissionRequestRoutes)],
    exports: [RouterModule],
})
export class CommissionRequestRoutingModule {
}



